* {
  box-sizing: border-box;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

body {
  font-family: 'Solway', serif;
}

.hidden {
  display: none;
}

.wrapper__header {
  width: 100%;
  height: 60px;
  display: flex;
  flex-direction: row;
  padding: 10px;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #8DE2DF;
  position: fixed;
  top: 0px;
  background-color: #FF4867;
  z-index: 1;
}

.wrapper__header i {
  font-size: 1.5625rem;
}

.wrapper__header--link {
  color: #8DE2DF;
}

.wrapper__header--title {
  font-size: 1.25rem;
  padding: 0 5px;
  text-decoration-line: underline;
  text-decoration-style: wavy;
}

.wrapper__main {
  padding-top: 95px;
  width: 100%;
  background-color: rgba(248, 220, 78, 0.836);
}

.wrapper__main--form {
  position: fixed;
  top: 63px;
  left: 15px;
}

.search-button {
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  transition: 1s;
  color: #000;
  background-color: #97E792;
  cursor: pointer;
}

.search-button:hover {
  background-color: #F15A29;
  transform: scale(0.8);
  box-shadow: inset 0 0 0 25px #F15A29;
}

.user-serie {
  height: 25px;
  border: 1px dashed #FF4867;
  text-align: center;
  background-color: rgba(255, 255, 255, 0.658);
}

.selected {
  background-color: #FFD302;
  border-radius: 50px;
  color: #FF4867;
  font-weight: 700;
}

.btn {
  border: none;
  transition: 1s;
  color: #000;
  background-color: #97E792;
  padding: 7px;
}

.btn:hover {
  background-color: #F15A29;
  transform: scale(0.8);
  box-shadow: inset 0 0 0 25px #F15A29;
}

.li-element {
  text-align: center;
  padding: 15px 0;
  margin: 0 0 15px 0;
}

.li-element img {
  width: 100px;
}

.empty-element {
  color: #ff0000;
  font-weight: 500;
}

.section-fav {
  margin: 0 15px 0 35px;
  text-align: center;
}

.title-fav {
  text-decoration-line: underline;
  text-decoration-style: wavy;
}

@media all and (min-width: 768px) {
  .wrapper__main {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    align-items: baseline;
  }

  .shows-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;
  }
}
